/*
  # Global
---------------------------------------------- */
/**
 * Default box-sizing
 */
*,
::before,
::after {
  box-sizing: inherit;
}

/**
 * Resetting margins, paddings, and borders
 */
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
}

/**
 * Resetting HTML5 Elements for older browsers
 */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block;
}

/*
  # Document & Sections
---------------------------------------------- */
html {
  overflow-y: scroll;
  box-sizing: border-box;
  min-height: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  -webkit-overflow-scrolling: touch;
  min-height: 100%;
  text-rendering: optimizeLegibility;
  font: 1em/1.5 sans-serif;
  -webkit-font-feature-settings: "palt";
  font-feature-settings: "palt";
}

/*
  # Grouping content
---------------------------------------------- */
blockquote,
q {
  quotes: none;
}

blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none;
}

hr {
  overflow: visible;
}

ol,
ul {
  list-style: none;
}

pre {
  font-size: 1em;
  font-family: monospace, monospace;
}

/*
  # Text-level semantics
---------------------------------------------- */
a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

a:active,
a:hover {
  outline-width: 0;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-size: 1em;
  font-family: monospace, monospace;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 0.8em;
}

sub,
sup {
  position: relative;
  vertical-align: baseline;
  font-size: 0.75em;
  line-height: 0;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

/*
  # Embedded content
---------------------------------------------- */
audio,
canvas,
progress,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img,
video {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

svg:not([fill]) {
  fill: currentColor;
}

svg:not(:root) {
  overflow: hidden;
}

/*
  # Tabular data
---------------------------------------------- */
table {
  border-spacing: 0;
  border-collapse: collapse;
}

/*
  # Forms
---------------------------------------------- */
button,
input,
optgroup,
select,
textarea {
  margin: 0;
  font: inherit;
}

button,
select {
  text-transform: none;
}

button,
input {
  overflow: visible;
}

button,
[type="button"],
[type="reset"],
[type="submit"],
[role="button"] {
  cursor: pointer;
  -webkit-appearance: button;
}

button:-moz-focusring,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
  outline: 1px dotted ButtonText;
}

legend {
  display: table;
  max-width: 100%;
  color: inherit;
  white-space: normal;
}

optgroup {
  font-weight: bold;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
  resize: vertical;
}

[type="checkbox"],
[type="radio"] {
  padding: 0;
}

[type="search"] {
  outline-offset: -2px;
  -webkit-appearance: textfield;
}

::-webkit-file-upload-button {
  font: inherit;
  -webkit-appearance: button;
}

/*
  # Interactive & Misc
---------------------------------------------- */
summary {
  display: list-item;
}

template,
[hidden] {
  display: none;
}

/*
  # Accessibility
---------------------------------------------- */
[disabled],
[aria-disabled="true"] {
  cursor: not-allowed;
}

[aria-busy="true"] {
  cursor: progress;
}

[aria-controls] {
  cursor: pointer;
}

[aria-hidden="false"][hidden] {
  display: initial;
}

[aria-hidden="false"][hidden]:not(:focus) {
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

:focus:not(:focus-visible),
::before:focus:not(:focus-visible),
::after:focus:not(:focus-visible) {
  outline: none;
}

/**
 * Prettier ignore
 */
@media (prefers-reduced-motion: reduce) {
  *,
  *:before,
  *:after {
    -webkit-transition: none !important;
    transition: none !important;
    -webkit-animation: none !important;
    animation: none !important;
    scroll-behavior: auto !important;
  }
}

/* default-style
   ------------------------------------------------------ */

html {
  box-sizing: border-box;
  height: 100%;
  font-family: "M PLUS 1p", "Lato", sans-serif;
  line-height: 1.5;
}

html *,
html *:before,
html *:after {
  box-sizing: inherit;
}

body {
  height: 100%;
  color: #263238;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

h2,
h3 {
  font-family: "M PLUS 1p", sans-serif;
}

*[lang="en"] {
  text-transform: uppercase;
  font-family: "Lato", sans-serif;
}

/* ======================================================
  Layout(ページ内配置) *内部配置は'Object'に記述
   ====================================================== */
/* centering
   ------------------------------------------------------ */
.l-container {
  position: relative;
  overflow: hidden;
  margin: 0 auto;
  max-width: 800px;
  width: 95%;
}
/* header
   ------------------------------------------------------ */
.l-header {
  overflow: hidden;
}
/* site-title
   ------------------------------------------------------ */
.l-header__site-title {
  float: left;
}
/* global-nav
   ------------------------------------------------------ */
.l-header__nav {
  float: right;
}
.l-header__nav-list {
  overflow: hidden;
}
.l-header__nav-item {
  float: left;
}
/* main
   ------------------------------------------------------ */
.l-main {
  clear: both;
}
/* section
   ------------------------------------------------------ */
.l-section {
  margin-top: 1.5rem;
  & p {
    margin-top: 0.5rem;
  }
}
/* grid
   ------------------------------------------------------ */
/* footer
   ------------------------------------------------------ */
.l-footer {
  margin-top: 2.5rem;
}

