/* Project(一回だけ)
   ------------------------------------------------------ */
/**
 * header
 */
.p-header {
  position: relative;
}
.p-header__logo {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}
.p-header__title-text {
  margin-left: 2.5rem;
}

.p-header__nav {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}
.p-header__nav-item + .p-header__nav-item {
  margin-left: 1rem;
}

/**
 * slider
 */
.slider-container {
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}
.slider-frame {
  position: relative;
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  & .sliderPause {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    text-align: center;
    background-color: rgba($color: #000000, $alpha: 0.3);
    color: #fff;
    user-select: none;
    transition: opacity 0.5s;
  }
}
.slider-wrapper {
  display: flex;
  position: relative;
  left: 0;
  transform: translateX(0%);
  transition: 1s;
}
.slider-item {
  position: relative;
  flex: 1 0 100%;
  margin-left: 0;
  left: 0;
  transition: 0s;
  & .slider-text {
    position: absolute;
    left: 0;
    font-size: 5vw;
    padding: 0 2vw;
    background-color: rgba(0, 0, 0, 0.2);
    color: #fff;
  }
  & img {
    width: 100%;
  }
}
.slider-controls {
  width: 100%;
  margin-top: 1rem;
  .next {
    float: right;
  }
}

/**
 * section
 */
section i {
  margin: 0 0.2rem;
}
